.btn-primary {//primary button
    outline: none;
    appearance: none;
    background-color: $primary;
    color: $secondary;
    border: 1.5px solid $tertiary;
    border-radius: 5px;
    padding: .375rem .75rem;
    transition: box-shadow 300ms ease-out;
   cursor: pointer;
    &:focus{
        box-shadow: 0 0 2px 3px $primary;
        transition: box-shadow 300ms ease-out;
    }
.icon, .loading-btn-text{
    margin-right: 0.375rem;

}
}


.btn-secondary{
    background-color: white;
    color: $secondary;
}
a.btn-primary{
    text-decoration: none;
    display: inline-block;
}

.btn-cta{
    font-size: $fs-400;
}


.close{//wrapper for close button
    display: flex;
    justify-content: flex-end;
    margin-block: $spacer-2;
}
.btn-close{//close button
    border: 0;
    border-radius: .375rem;
    font-size: 2rem;
    background-color: transparent;
    .icon{
        margin: 0;
    }
    cursor: pointer;
    opacity: .5;
    &:hover{
        opacity: 1;
    }
}
